
.dimmed {
  position: relative;
}
.dimmed:before {
  content: " ";
  z-index: 10;
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.5);
}
.highlight {
  background-color: #FFFF00 !important;
}
.highlightable {
  transition-property: background-color;
  transition-duration: 1s;
  transition-timing-function: easy-out;
  transition-delay: 0s;
}
html {
  height: 100%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  position: relative;
  margin: 0;
  padding-bottom: 6rem;
  min-height: 100%;
}
DIV.foot {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}
DIV.foot SPAN {
  font-size: 80%;
  color: #ccc;
  border-top: 1px solid #ccc;
  float: right;
  margin-right: 0.5em;
  margin-bottom: 0.3em;
}
.red {
  color: red;
}
.green {
  color: green;
}
.odd {
  background-color: rgba(0, 0, 0, .05);
}
h1::first-letter {
  margin-top: 0;
}
h2::first-letter {
  margin-top: 0;
}
h3::first-letter {
  margin-top: 0;
}
h4::first-letter {
  margin-top: 0;
}
h5::first-letter {
  margin-top: 0;
}
h6::first-letter {
  margin-top: 0;
}
.text-success-dark {
  color: #5cb85c;
}
@media print {
body {
    font-size: 70%;
    margin: 0;
    padding: 0 !important;
    min-width: 768px;
}
.container {
    width: auto;
    min-width: 750px;
}
}
.break {
  margin-bottom: 0.6rem;
}
.card-body LABEL {
  text-transform: uppercase;
  color: #777;
  margin-bottom: 0px;
  font-size: 80%;
}
.text-white .card-body A {
  color: #fff;
}
.dropdown-submenu {
  position: relative;
}
.dropdown-submenu>.dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
  border-radius: 0 6px 6px 6px;
}
.dropdown-submenu:hover>.dropdown-menu {
  display: block;
}
.dropdown-submenu>a:after {
  display: block;
  content: " ";
  float: right;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 0 5px 5px;
  border-left-color: #ccc;
  margin-top: 5px;
  margin-right: -10px;
}
.dropdown-submenu:hover>a:after {
  border-left-color: #fff;
}
.dropdown-submenu.pull-left {
  float: none;
}
.dropdown-submenu.pull-left>.dropdown-menu {
  left: -100%;
  margin-left: 10px;
  border-radius: 6px 0 6px 6px;
}
DIV.sobre p {
  font-size: 120%;
}
DIV.sobre ol li {
  font-size: 120%;
  padding-bottom: 12pt;
}
TABLE.table-peticao TBODY TD {
  vertical-align: middle;
}
.drop-box {
  background: #F8F8F8;
  border: 5px dashed #DDD;
  width: 100#;
  text-align: center;
  padding: 1em;
}
.dragover {
  border: 5px dashed blue;
}
body1 {
  background-color: #eee !important;
}
.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
H1 {
  text-align: center;
}
.metas {
  text-align: center;
  width: 100%;
}
.transparent {
  filter: alpha(opacity=0);
  opacity: 0.0;
  -moz-opacity: 0.0;
  -webkit-opacity: 0.0;
}
div.metas {
  text-align: center;
  display: inline-block;
}
div.metas table {
  width: auto;
  margin: 0 auto !important;
}
em.invalid {
  display: block;
  margin-top: 6px;
  padding: 0 1px;
  font-style: normal;
  font-size: 12px;
  line-height: 15px;
  color: #ee9393;
}



